import React from "react";
import { NavLink } from "react-router-dom";
import { Link } from "react-router-dom";

import { Nav, Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button, Modal,  ModalHeader, ModalBody, ModalFooter,
  NavbarText, Row, Col, } from "reactstrap";
// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";

import logo from "logo-white.svg";

var ps;
class Sidebar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
    }
    this.activeRoute.bind(this);
  }


  toggleModal = () => this.setState({modal: !this.state.modal});


  handleModalOpen = () => {
    this.setState((prevState) => {
      return {
        modalOpen: !prevState.modalOpen
      }
    })
  }

  // verifies if routeName is the one active (in browser input)
  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }
  componentDidMount() {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(this.refs.sidebar, {
        suppressScrollX: true,
        suppressScrollY: false,
      });
    }
  }
  componentWillUnmount() {
    if (navigator.platform.indexOf("Win") > -1) {
      ps.destroy();
    }
  }

  render() {
    return (
      <div className="sidebar" data-color="green">
        <div className="logo">
          <a
            href="https://www.creative-tim.com?ref=nudr-sidebar"
            className="simple-text logo-mini"
            target="_blank"
          >
            <div className="logo-img">
              <img src={logo} alt="react-logo" />
            </div>
          </a>
          <a
            href="https://www.creative-tim.com?ref=nudr-sidebar"
            className="simple-text logo-normal"
            target="_blank"
          >
            Codestack LMS
          </a>
        </div>
       <div className="sidebar-wrapper" ref="sidebar" style={{ backgroundColor: "#379038" }}>
          <Nav>
            {this.props.routes.map((prop, key) => {
              if (prop.redirect) return null;
              if(prop.type && prop.type == 'dropdown'){
                return (
                  <UncontrolledDropdown nav inNavbar>
                      <DropdownToggle nav caret className={"mx-0 px-3"} style={{borderRadius: 0}}>
                        {prop.name}
                      </DropdownToggle>
                      <DropdownMenu right>
                        {prop.children.map(sublink => (
                          <DropdownItem onClick={this.toggleModal}>
                          {sublink.name}
                        </DropdownItem>
                        ))}
                      </DropdownMenu>
                    </UncontrolledDropdown>
                )
              }else{
                return (
                  <li
                    className={
                      this.activeRoute(prop.layout + prop.path) +
                      (prop.pro ? " active active-pro" : "")
                    }
                    onClick={() => {
                      prop.pro ? this.props.logout() // handle logout
                        :
                        prop.action && this.props.handleAction(prop.action) //handle other actions
                    }}
                    key={key}
                  >
                    <NavLink
                      to={!prop.pro && prop.layout + prop.path}
                      className="nav-link"
                      activeClassName="active"
                      style={{ borderRadius: 0, margin: 0 }}
                    >
                      <i className={"now-ui-icons " + prop.icon} />
                      <p>{prop.name}</p>
                    </NavLink>
                  </li>
                );
              }
            })}
          </Nav>
        </div>


        <Modal isOpen={this.state.modal} toggle={this.toggleModal} >
        <ModalHeader toggle={this.toggleModal}>Please Select Subject</ModalHeader>
        <ModalBody>
          <Row xs={12}>
              <Col xs={4}>
                <Link to="/admin/lesson" className="nav-link" >
                <Button style={{width: "100%"}} className="btn btn-success bg-midnight" onClick={this.toggleModal} >Biology</Button>{' '}
                </Link>
              </Col>
              <Col xs={4}>
                <Link to="/admin/lesson" className="nav-link" >
                <Button style={{width: "100%"}} className="btn btn-success bg-midnight" onClick={this.toggleModal} >Chemistry</Button>{' '}
                </Link>
              </Col>
              <Col xs={4}>
                <Link to="/admin/lesson" className="nav-link" >
                <Button style={{width: "100%"}} className="btn btn-success bg-midnight" onClick={this.toggleModal} >Physics</Button>{' '}
                </Link>
              </Col>
            </Row>
            <Row xs={12}>
              <Col xs={4}>
                <Link to="/admin/lesson" className="nav-link" >
                <Button style={{width: "100%"}} className="btn btn-success bg-midnight" onClick={this.toggleModal} >Geography</Button>{' '}
                </Link>
              </Col>
              <Col xs={4}>
                <Link to="/admin/lesson" className="nav-link" >
                <Button style={{width: "100%"}} className="btn btn-success bg-midnight" onClick={this.toggleModal} >Economics</Button>{' '}
                </Link>
              </Col>
              <Col xs={4}>
                <Link to="/admin/lesson" className="nav-link" >
                <Button style={{width: "100%"}} className="btn btn-success bg-midnight" onClick={this.toggleModal} >Biology</Button>{' '}
                </Link>
              </Col>
            </Row>
        </ModalBody>
      </Modal>
      </div>
    );
  }
}

export default Sidebar;
