import React from 'react';
import { Link } from "react-router-dom";
import {
  Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button,
} from 'reactstrap';

const CardWithImage = (props) => {
  return (
    <div>
    <Card  className="text-center">
      <CardImg top width="100%" src={require("assets/img/bg5.jpg")} alt="Card image cap" />
      <CardBody>
        <CardTitle style={{textAlign:"center", justifyItems:"center"}}>{props.title}</CardTitle>
        <CardText>{props.description}</CardText>
        {/* <Link to="/admin/available-subjects" className="nav-link">
        <Button style={{background: "#000034",justifyContent: 'center', alignItems: 'center'}}>VIEW AVAILABLE SUBJECTS</Button>
     </Link> */}
      </CardBody>
    </Card>
</div>
  );
};

export default CardWithImage;