import React from "react";

// reactstrap components
import {
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Table,
  Row,
  Col,
  CardImg, 
  CardText,
  CardSubtitle,
  Button
} from "reactstrap";

// core components
import PanelHeader from "components/PanelHeader/PanelHeader.js";
import { thead, tbody } from "variables/general";
import CardWithImage from "components/Card/CardWithImage";

class AvailableSubject extends React.Component {
  render() {
    return (
      <>
        <PanelHeader size="sm" />
        <div className="content">
          <Row xs={12}>
            <Col xs={3}>
              <CardWithImage title={'Primary'}/>
            </Col>
            <Col xs={3}>
              <CardWithImage title={'Primary Two'}/> 
            </Col>
            <Col xs={3}>
              <CardWithImage title={'Primary Three '}/>
            </Col>
          </Row>
          <Row xs={12}>
            <Col xs={6}>
              <CardWithImage title={'Primary Four'}/>
            </Col>
            <Col xs={6}>
              <CardWithImage title={'Primary Five'}/>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}

export default AvailableSubject;
